body {
  background-color: #cd353b;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    overflow-x: hidden;
    display: flex;
    justify-content: center;

}
.App {
  width: 100%;
  padding: 20px;
}
.title {
  color: #fff;
  text-align: center;
  margin-bottom: 30px;

}
 h3{
  margin-top: -20px;
}
.subtitle {
  border-left: 6px solid #fff;
  padding-left: 10px;
  color: #eee;
}
.grid-item {
  cursor: pointer;
  width: 250px;
  border-radius: 4px;
  padding: 20px 20px 20px 10px;
  margin-bottom: 20px;
  overflow: auto;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  vertical-align: middle;
}

@media screen and (min-width:480px) and (max-width: 640px){
  .comment-card{
      width: 800px
  }
}

.grid-item .avatar {
  width: 60px;
  height: 60px;
  background-color: #eee;
  display: block;
  border-radius: 50%;
  border: 2px solid #fff;
}
.grid-item .userinfo {
  float: left;
  height: 100%;
}
.grid-item .content {
  color: #333;
  padding-left: 80px;
  /* text-indent: 2em; */
}
.grid-item .nickname {
  color: #999;
  font-size: 13px;
  text-align: right;
  padding-top: 6px;
}

.grid-item .time {
  color: #aaa;
  font-size: 12px;
  text-align: right;
  padding-top: 2px;
}

.load-more {
  margin-top: 80px;
  margin-bottom: 80px;
  text-align: center;
}

#hot-comment-list {
  margin-bottom: 40px;
}



.say{
  margin-top: 140px;
  color: white;
  max-width: 1100px;
  line-height: 50px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}

.say.thanks{
  margin-top: 0px;
  margin-left: 0px;
  margin-bottom: 100px;
}



